.signInScreen{
  max-width: 300px;
  padding: 70px;
  margin-left: auto;
  margin-right: auto;
  background: rgba(0,0,0,0.85);
}

.signInScreen > form{
  display: grid;
  flex-direction: column;
}

.signInScreen > form > input{
  height: 40px;
  margin-bottom: 14px;
  outline-width: 0;
  border-radius: 5px;
  border: none;
  padding: 5px 15px;
}

.signInScreen > form > h1{
  text-align: left;
  margin-bottom: 25px;
}

.signInScreen > form > h4{
  text-align: left;
  margin-top: 30px;
}


.signInScreen > form > button{
  padding: 16px 20px;
  font-size: 1;
  color: #fff;
  border-radius: 5px;
  background-color: #e50914;
  font-weight: 600;
  border: none;
  cursor: pointer;
  margin-top: 20px;
}

.signInScreen__gray{
  color: gray;
}

.signInScreen__link:hover{
  cursor: pointer;
  text-decoration: underline;
}