.login{
  position: relative;
  height: 100%;
  background: url("https://hcdevilsadvocate.com/wp-content/uploads/2019/01/netflix-background-9-900x506.jpg") center no-repeat;
  background-size: cover;
}

.login__logo{
  position: fixed;
  left: 0;
  width: 150px;
  object-fit: contain;
  padding-left: 20px;
}

.login__button{
  position:fixed;
  right:20px;
  top:20px;
  padding: 10px 20px;
  color:white;
  background-color: #e50914;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

.login__gradient{
  width: 100%;
  z-index: 1;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
  background-image: linear-gradient(
    to top,
    rgba(0,0,0,0.8)0,
    rgba(0,0,0,0)60%,
    rgba(0,0,0,0.8)100%
  );
}

.login__body{
  z-index: 1;
  position:absolute;
  top:30%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  color: #fff;
  padding: 20px;
  left: 0;
  right: 0;
}

.login__body > h1{
  font-size: 3.125rem;
  margin-bottom: 20px;

}

.login__body > h2{
  font-size: 2rem;
  margin-bottom: 30px;
  font-weight: 400;

}

.login__body > h3{
  font-size: 1.3rem;
  margin-bottom: 30px;
  font-weight: 400;
}

.login__input > form > input{
  padding: 10px;
  outline-width: 0;
  width: 30%;
  max-width: 600px;
  border: none;
  height: 30px;
}

.login__getstarted{
  padding: 16px 20px;
  font-size: 1rem;
  color:#fff;
  background-color: #e50914;
  border: none;
  font-weight: 600;
  cursor: pointer;
}

